import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { HamiltonPage } from '../../pages/hamilton/hamilton';

/**
 * Generated class for the ShillNavComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'shill-nav',
  templateUrl: 'shill-nav.html'
})
export class ShillNavComponent {

  text: string;
  views: any;

  constructor(public navCtrl: NavController) {
    console.log('Hello ShillNavComponent Component');
    console.log(this.navCtrl.first);
    this.views=[];
    this.navCtrl.getViews()
      .forEach((v) => { this.views.push(v.instance.title); });
    console.log(this.views);
    console.log(this.navCtrl.getViews());
    
  }

}



